.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.navbar {
  background-color: #2c3e50;
  color: white;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-brand h1 {
  margin: 0;
  font-size: 1.5rem;
}

.navbar-menu {
  list-style: none;
  display: flex;
  gap: 2rem;
  margin: 0;
  padding: 0;
}

.navbar-menu li a {
  color: white;
  text-decoration: none;
  font-weight: 500;
  transition: opacity 0.2s;
}

.navbar-menu li a:hover {
  opacity: 0.8;
}

.main-content {
  flex: 1;
  padding: 2rem;
  background-color: #ecf0f1;
}

.footer {
  background-color: #34495e;
  color: white;
  padding: 1rem;
  text-align: center;
}

.footer p {
  margin: 0;
  font-size: 0.9rem;
}
